import React, { useEffect, useRef, useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import { API_URL } from '../apiConfig';
import { decode } from 'blurhash';

/**
 * BlurHashCanvas
 * Draws a decoded blurhash into a canvas sized to fill its parent.
 * Props:
 * - blurHash (string)
 * - width (number)
 * - height (number)
 */
const BlurHashCanvas = ({ blurHash, width, height }) => {
    const canvasRef = useRef(null);

    useEffect(() => {
        if (!blurHash || !canvasRef.current || !width || !height) return;
        // Choose a small decoded size for speed — will be upscaled by the canvas.
        const decodedWidth = 32;
        const decodedHeight = Math.max(1, Math.round((decodedWidth * height) / width));
        let pixels;
        try {
            pixels = decode(blurHash, decodedWidth, decodedHeight); // returns Uint8ClampedArray rgba
        } catch (e) {
            console.warn('Failed to decode blurhash:', e);
            return;
        }
        const ctx = canvasRef.current.getContext('2d');
        // Create ImageData and put on canvas
        const imageData = ctx.createImageData(decodedWidth, decodedHeight);
        imageData.data.set(pixels);
        // draw to small canvas then upscale to fill
        const tmp = document.createElement('canvas');
        tmp.width = decodedWidth;
        tmp.height = decodedHeight;
        const tmpCtx = tmp.getContext('2d');
        tmpCtx.putImageData(imageData, 0, 0);

        // Now scale to visible canvas
        const canvas = canvasRef.current;
        canvas.width = canvas.clientWidth;
        canvas.height = canvas.clientHeight;
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.imageSmoothingEnabled = true;
        ctx.drawImage(tmp, 0, 0, tmp.width, tmp.height, 0, 0, canvas.width, canvas.height);
    }, [blurHash, width, height]);

    // canvas fills container via absolute inset styles in parent
    return <canvas ref={canvasRef} className="absolute inset-0 w-full h-full block" aria-hidden="true" />;
};

/**
* ResponsiveAuthImage
* Props:
* - baseName: string (the backend image id / base filename)
* - alt: string
* - className: string (applied to the <img>)
* - sizes: string (for srcSet)
* - onLoad: function
* - width, height: numbers (image metadata - used to set aspect ratio in parent)
* - blurHash: string (if available render blurhash canvas)
* - eager: boolean (if true, renders a high-priority <img> tag instead of lazy-loading)
*/
const ResponsiveAuthImage = ({ baseName, alt, className = '', sizes, onLoad, width, height, blurHash, eager = false }) => {
    // --- MODIFIED: Moved state hook to top ---
    const [loaded, setLoaded] = useState(false);

    // Render a simple placeholder if baseName is invalid.
    if (!baseName || typeof baseName !== 'string') {
        return <div className={`bg-gray-200 w-full h-full ${className}`} />;
    }

    const uuidRegex = /([a-f0-9]{8}-[a-f0-9]{4}-[a-f0-9]{4}-[a-f0-9]{4}-[a-f0-9]{12})/;
    const match = baseName.match(uuidRegex);

    let imageID;
    if (match) {
        imageID = match[0]; // Standard UUID path
    } else {
        // Fallback path: Use the baseName itself, stripping a potential .webp extension
        // This allows for images like 'cover-image.webp'
        imageID = baseName.endsWith('.webp') ? baseName.slice(0, -5) : baseName;
    }

    // Construct image URLs
    const src = `${API_URL}/api/uploads/${imageID}-small.webp`;
    const placeholderSrc = `${API_URL}/api/uploads/${imageID}-tiny.webp`; // tiny fallback if blurHash not present
    const srcSet = [
        `${API_URL}/api/uploads/${imageID}-small.webp 300w`,
        `${API_URL}/api/uploads/${imageID}-medium.webp 600w`,
        `${API_URL}/api/uploads/${imageID}.webp 1200w`
    ].join(', ');

    // --- (useState hook was here, but has been moved up) ---

    const handleAfterLoad = (event) => {
        setLoaded(true);
        if (typeof onLoad === 'function') onLoad(event);
    };

    // EAGER loading: Render a standard <img> tag for LCP (Largest Contentful Paint)
    // This bypasses the blur/lazy-load logic entirely for critical images.
    if (eager) {
        return (
            <img
                alt={alt}
                src={src}
                srcSet={srcSet}
                sizes={sizes}
                className={className}
                onLoad={handleAfterLoad}
                fetchpriority="high"
                // Pass width/height if available, though layout is controlled by className
                {...(width && { width })}
                {...(height && { height })}
            />
        );
    }

    // LAZY loading (default): Wrapper is full-size of parent.
    return (
        <div className="w-full h-full relative overflow-hidden bg-gray-100">
            {/* If blurHash provided and we have metadata, render the canvas placeholder */}
            {blurHash && width && height ? (
                <BlurHashCanvas blurHash={blurHash} width={width} height={height} />
            ) : (
                // Fallback tiny image — will be visible until main image loads.
                <img
                    src={placeholderSrc}
                    alt={alt || ''}
                    className="absolute inset-0 w-full h-full object-cover block"
                    aria-hidden="true"
                    style={{ filter: 'blur(6px)', transform: 'scale(1.02)' }}
                />
            )}

            {/* Real image (lazy loaded) */}
            <LazyLoadImage
                alt={alt}
                src={src}
                srcSet={srcSet}
                sizes={sizes}
                // do not provide placeholderSrc to library when we already rendered our placeholder
                effect={null}
                className={`${className} transition-opacity duration-500 ${loaded ? 'opacity-100' : 'opacity-0'}`}
                afterLoad={handleAfterLoad}
                // Eager path is handled above, so this is always 'lazy'
                loading={'lazy'}
            />
        </div>
    );
};

export default ResponsiveAuthImage;